<template>
	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" class="login-container">
		<h3 class="title">二网公司运营管理后台</h3>
		<el-form-item prop="account">
			<el-input type="text" v-model="ruleForm.account" auto-complete="off" placeholder="账号"></el-input>
		</el-form-item>
		<el-form-item prop="checkPass">
			<el-input type="password" @keyup.enter.native="handleSubmit" v-model="ruleForm.checkPass" auto-complete="off" placeholder="密码"></el-input>
		</el-form-item>
		<!-- <el-checkbox checked class="remember">保持登录</el-checkbox> -->
		<el-form-item style="width:100%;">
			<el-button type="primary" @click.native.prevent="handleSubmit" style="width:100%;" :loading="logining">登录</el-button>
		</el-form-item>
	</el-form>
</template>
<script>
import { requestLogin } from '@/api/api';
export default {
	name: 'login',
	data() {
		return {
			logining: false,
			ruleForm: {
				account: 'admin',
				checkPass: '123456'
			},
			rules: {
				account: [
					{ required: true, message: '请输入账号', trigger: 'blur' },
					//{ validator: validaePass }
				],
				checkPass: [
					{ required: true, message: '请输入密码', trigger: 'blur' },
					//{ validator: validaePass2 }
				]
			},
			checked: true
		}
	},
	methods: {
		handleSubmit(ev) {
			this.$refs.ruleForm.validate((valid) => {
				if (valid) {
					this.logining = true;
					let loginParams = { username: this.ruleForm.account, password: this.ruleForm.checkPass };
					// console.log(222)
					// localStorage.setItem('user', JSON.stringify(1111));
					// this.$router.push({ path: '/index' });
					requestLogin(loginParams).then(res => {
					 	this.logining = false;
						let { msg, code, data } = res;
						if (code != 0) {
							this.$message({
								message: msg,
								type: 'error'
							});
						} else {
							this.$message({
								message: msg,
								type: 'success'
							});
							localStorage.setItem('user', JSON.stringify(data));
							this.$router.push({ path: '/carTypeList' });
						}
					});
				} else {
				  console.log('error submit!!');
				  return false;
				}	
			});
			
		},
		selsChange: function(sels) {
			this.sels = sels;
		}
	}
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.login-container {
	/*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
	-webkit-border-radius: 5px;
	border-radius: 5px;
	-moz-border-radius: 5px;
	background-clip: padding-box;
	margin: 180px auto;
	width: 350px;
	padding: 35px 35px 15px 35px;
	background: #fff;
	border: 1px solid #eaeaea;
	box-shadow: 0 0 25px #cac6c6;
	.title {
		margin: 0px auto 40px auto;
		text-align: center;
		color: #505458;
	}
	.remember {
		margin: 0px 0px 35px 0px;
	}
}

</style>



